@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  /* background:linear-gradient(); */
  
  background: linear-gradient(to left, rgb(240, 131, 212), rgb(143, 212, 212) 50%, #eee 75%, rgb(223, 197, 197) 75%);
  /* background: linear-gradient(0.25turn,darkslategrey ,#ebf8e1,#3f87a6); */
  overflow-x: hidden;
 
  
}

.footer{
    background: linear-gradient(0.25turn,#e8a1eb,#4193d6 );
    color: white;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    min-height: 70px;
    border-radius: 8px;
    padding: 20px 25px ;
   
}
.footer .footer_copyright{
    font-size:20px;
    text-align:end;
    color: #9c9c9c;

}
.footer .footer_copyright > a{
    text-decoration: none !important;
    color: var(--main-color);
}
.custom_btn{
    background-color: var(--main-color) !important;
    font-size: 14px !important;
    font-weight: 500 !important;
    border: 0 !important;
    border-radius: 50px !important;
    padding: 1px 12px !important;
    line-height: 35px !important;
    min-width: 100px !important;
    outline: none !important;
}
 .custom_btn .btn_icon_container{
     color:  black !important;
     background: #fff !important;
     border-radius: 50px !important;
     height: 27px !important;
     width: 27px !important;
     line-height: 29px !important;
     text-align: center !important;
     display: flex !important;
     justify-content:  center !important;

}
.custom_btn .btn_icon_container > .MuiSvgIcon-root{
    font-size:  20px !important;
    
}

 .custom_btn .btn_text{
   font-size: 14px !important;
    text-transform: none !important;
    text-align: center;
 color: black;
    width: 100px;
    margin-right: 5px;
    margin-left: 5px;
}
.header{

  margin-bottom: 90px !important;
  background: linear-gradient(0.25turn,#4193d6,#e8a1eb );
  /* background-color:rgb(25, 180, 180) !important; */
  padding: 0 22px 0 0 !important;
  border-radius: 6px !important;
  
  box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07) !important;   
} .header_link { 
font-weight: 500;
color: rgb(45, 151, 238) !important;

}
.header_link:hover {
    color: red;

}
.header .header_navLink{
    padding: 0;
}
.header .header_link_active {
    font-weight: 500;
    color: var(--main-color);
}
.header .header_home {
    background-color: var(--main-color);
    padding: 22px;
    display: flex;
    justify-content: center;
    border-bottom: 6px;
    border-bottom-left-radius:6px;
}
.header .header_left{
    margin-right: auto;
    
}
.header .header_right{
    display: flex;
    align-items: center !important;
   
}
.header .header_right > a> .MuiSvgIcon-root{

    font-size: 16px !important;
    margin-right: 12px;
    color: black;
    
}

.section_project{
    padding-right: 40px;
    border: 1px solid black;
}
.custom_tabs.custom_tabs_item{
    padding: 0px !important;
    min-width: 0px !important;
    margin-right: 20px !important;
    text-transform: inherit !important;
    font-weight: 400 !important;
    font-size: 14px !important;
    outline-width: 0px !important;
}.custom_tabs.active{
color:var(--main-color) !important;

}
/* ent tabs */
/* cards */

.custom_card{
    margin-right: 20px;
height:100%;
max-width :350px;
box-shadow:0 px 0px 48px rgba(4,6,4,0.08) !important;
border-radius: 5px;
overflow: hidden;
outline-width: 0px importnt;


}
.custom_card .customcard_image{
    height: 140px;


}
.custom_card .customcard_title{
    font-weight: 500;

}.custom_card .customcard_description{
color:grey !important;
}
/* dialog */

/* .projectDialog {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 800px;
} */
.section_project{
   align-items: center;
   padding-left: 60px ;
    border: 1px solid rgb(109, 25, 25);
}
.projectDialog .projectDialog_image{
    max-height: 300px;
    width: 100%;
    object-fit: cover;
    object-position: center center;
}
.projectDialog .projectDialog_description{

    margin-top: 10px !important;

}
.projectDialog .projectDialog_actions{
    
    justify-content: center !important;
    margin-bottom: 10px;
}
.projectDialog .projectDialog_icon{

    color: black;
    margin: 0px 12px;
    cursor: pointer;
}
.projectDialog .projectDialog_icon > .MuiSvgIcon-root{
    font-size: 30px !important;
}
.timeline{
    padding: 0 !important;
    
}
/* header */
/* header title */
.timeline.timeline_title{
    padding-top: 1px;
}
.timeline .timeline_headerItem{
    min-height: 80px !important;
}
.timeline .MuiTimelineItem-root{
min-height: 0px;

}
.timeline .MuiTimelineItem-missingOppositeContent::before{
display: none;
}
.timeline .timeline_dot_header{
    color: #fff !important;
    font-size: small !important;
    padding: 10px !important;
    background-color: var(--main-color) !important;
}
/* remaing part */
.timeline .timeline_dot{
    color: #fff !important;
 
    padding: 2px !important;
    border-color: var(--main-color) !important;

}
.timeline .MuiTimelineConnector-root{
    background-color:darkslategrey ;
}
.timeline .timeline_dot_header> .MuiSvgIcon-root{
    font-size: 20px;
    color:darkslategrey
}
.timeline .separator_padding{
    padding-left: 18px !important;
}
.section{
    padding: 4px;
    box-shadow: 0px 0px 48px rgb(4,6, 4, 0.08) !important;
}
.about_text{
color: darkslategrey !important ;
font: smaller !important;
line-height: 22px !important;
white-space: pre-line !important;

}
/* timeline section */
.timeline_title{
    font-size: 16px !important;
    font-weight: 500 !important;
}
.timeline_date{
    color: darkslategrey;
}
.timeline_description {
    color:grey;
    font-size: small;
    margin-top: 6px;
}
/* end of section */
/* services */
.service{
background-color:rgb(154, 154, 209);
    height: 100%;
    text-align: center;
}
.service .service_title{
    font-size: 16px !important;
    font-weight: 500 !important;
    line-height: 35px !important;
    text-align: center;
    color: white;
}
.service .service_description{
    color: #050505 !important;
    border-color: var(--main-color) !important;
    margin-right: 5px !important;
    text-align: center;
    font-weight: 500;
}

.service_Icon > .MuiSvgIcon-root{
    font-size: 46px !important;
    margin-left: 0 !important;
    font-weight: 300 !important;
    color: black!important;
}
/* skills */
.skill{
    border-radius: 8px;
    
    padding: 20px;
    box-shadow: 0px 0px 48px rgb(4,6, 4, 0.08) !important;
    min-height: 186px;
    height: 100%;
}
.skill .skill_title{
    
    font-size: 20px !important;
    font-weight: 500 !important;
    line-height: 35% !important;
    text-align: center;
    margin-bottom: 20px;
}
.skill .skill_description{
    
display: flex;
align-items: center;
color: #989898 !important;
font-weight: 400 !important;
line-height: 18px !important;

}
.timeline_dot{
    border: 1px solid red;

 cursor: pointer;
  border-color: var(--main-color); 
  padding-left: 40px !important;
  font-size: 20px;
  margin-right: 7px !important;
 
}
.profile{
    background-color: #fff;
    border-radius: 6px;
    width: 100%;
    position:relative;
    display: inline-block;
   

}
.profile_name{
    
    line-height: 18px;
    padding: 20px;
}.profile_name .name{
text-transform: uppercase;
font-size: 17px;
font-weight:bold;
}
.profile_name .title{
    font-size: 13px;
    color:#777;
}
.profile_image {
   
margin-top: -10px; 

-webkit-clip-path:polygon(0 9%,100% 0,100% 94%,0% 100%);
clip-path: polygon(0 9%,100% 0,100% 94%,0% 100%);
}
.profile_image img{
    width: 100%;
}
/* information */
.profile_information{
    top: 0;
    left:0;
    margin: -49px 10px 30px;
}
.profile_information .timeline .timeline_content{
   
    margin-bottom: 1px;
}.timelineItem_text{
    font-size: 13.5px !important;
    color: #787878;
    font-weight: 400 !important;
}.timelineItem_text > a{
    color: #787878;
text-decoration:none;

}.timelineItem_text >span{
    color: rgb(10, 9, 9);
    font-size: 15px;
    font-weight: bold;
   
}
.profile .btn_container{
    width: 100%;
    display: flex !important;
    justify-content: center;
    margin-top: 1  0px;

}
:root{
    --main-color:rgb(199, 164, 190);
}
/* container margins and top */
.top_60{
    margin-top: 60px !important;
}
.top_30{
    margin-top: 30px !important ;

}.p_30{
    padding: 30px  !important ;

}
.pt_45{
    padding-top: 45px !important;
}
.pb_45{
    padding-bottom: 45px !important;
}
.p_50{
    padding: 50px !important;
}


@media(max-width:991px){

    .top_60{
        margin-top: 30px !important;
    }

} 
.container_shadow{
    box-shadow: 0px 2px 92px 0px rgb(0, 0, 0,0,0.13);
-webkit-box-shadow:0px 2px 92px 0px rgb(0, 0, 0,0,0.13);
-moz-box-shadow: 0px 2px 92px 0px rgb(0, 0, 0,0,0.13);
}
.main-content{
    background-color: white;
    min-height: 100vh;
    border-radius: 6px;
    -moz-border-radius:6px;
    -webkit-border-radius:6px;

}.section_title{
    position: relative;
    display: inline-block;
    padding: 0 3px;
}

.section_title .section_title_text {

    font-size: 18px;
    font-weight: 600;
    position: relative;
    margin: 0;
}.section_title span{

    font-size: 18px;
    position: absolute;
    height: 7px;
    width: 100%;
    bottom: 0px;
    opacity:0;
    left: 0;
}
.container{
    box-shadow: 0px 0px 48px rgb(4,6, 4, 0.08) !important;
    /* background-color: darkslategrey; */
}
