:root{
    --main-color:rgb(199, 164, 190);
}
/* container margins and top */
.top_60{
    margin-top: 60px !important;
}
.top_30{
    margin-top: 30px !important ;

}.p_30{
    padding: 30px  !important ;

}
.pt_45{
    padding-top: 45px !important;
}
.pb_45{
    padding-bottom: 45px !important;
}
.p_50{
    padding: 50px !important;
}


@media(max-width:991px){

    .top_60{
        margin-top: 30px !important;
    }

} 
.container_shadow{
    box-shadow: 0px 2px 92px 0px rgb(0, 0, 0,0,0.13);
-webkit-box-shadow:0px 2px 92px 0px rgb(0, 0, 0,0,0.13);
-moz-box-shadow: 0px 2px 92px 0px rgb(0, 0, 0,0,0.13);
}
.main-content{
    background-color: white;
    min-height: 100vh;
    border-radius: 6px;
    -moz-border-radius:6px;
    -webkit-border-radius:6px;

}.section_title{
    position: relative;
    display: inline-block;
    padding: 0 3px;
}

.section_title .section_title_text {

    font-size: 18px;
    font-weight: 600;
    position: relative;
    margin: 0;
}.section_title span{

    font-size: 18px;
    position: absolute;
    height: 7px;
    width: 100%;
    bottom: 0px;
    opacity:0;
    left: 0;
}
.container{
    box-shadow: 0px 0px 48px rgb(4,6, 4, 0.08) !important;
    /* background-color: darkslategrey; */
}